.credit-wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.credit-wrapper > .credit-col {
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    margin: 4vw;
}

.credit-wrapper > .credit-col > div {
    flex-grow: 1;
}

.credit-wrapper > .credit-col > div.left {
    flex: 40%;
    text-align: right;
    padding-right: 2vw;
}
.credit-wrapper > .credit-col > div.left h4{
    font-weight: 600 !important;
    text-transform: uppercase;
}
.credit-wrapper > .credit-col > div.right {
    flex: 60%;
    text-align: left;
    padding-left: 2vw;
}

.credit-wrapper > .credit-col > div.right > span {
    display: block;
}